import React, { useState, useContext } from 'react';
import { useNavigate } from 'react-router-dom';
import { Button } from '@material-ui/core';
import {
	DialogActions,
	DialogContent,
	Dialog,
	DialogTitle,
	Box,
} from '@mui/material';
import { UserStateContext, DispatchContext } from '../../Context';
import LandingPageHeader from './components/LandingPageHeader';
import DrawerComponents from '../../components/LandingNavBar/DrawerComponents';
import ScrollUpButton from '../../components/commons/ScrollUpButton';
import RankingButton from '../../components/commons/RankingButton';

import ImgSrc from '../../core/constants/ImgSrc';

import Sections from './components/Sections/Sections';

function LandingPage() {
	const navigate = useNavigate();
	const userState = useContext(UserStateContext);
	const dispatch = useContext(DispatchContext);

	//  user null    .
	const isLogin = !!userState.user;

	const [open, setOpen] = useState(false);

	//     
	const logout = () => {
		// sessionStorage   JWT  .
		sessionStorage.removeItem('userToken');
		// dispatch   .
		dispatch({ type: 'LOGOUT' });
		//   .
		navigate('/');
	};

	const [dialogOpen, setDialogOpen] = useState(false);

	const handleDialogOpen = () => {
		setDialogOpen(true);
	};
	const handleDialogClose = () => {
		setDialogOpen(false);
	};

	return (
		<Box>
			<LandingPageHeader
				open={open}
				setOpen={setOpen}
				isLogin={isLogin}
				handleDialogOpen={handleDialogOpen}
				logout={logout}
			/>

			<div
				style={{
					position: 'absolute',
					top: '380px',
					width: '100%',
					zIndex: '1100',
				}}
			>
				<div
					style={{
						left: '50vw',
						transform: 'translateX(-50%)',
						position: 'absolute',
					}}
				>
					<img alt='' src={ImgSrc.navIconImg} width='200px' height='200px' />
				</div>
			</div>

			<Box sx={{ marginTop: '60px' }}>
				<Sections isLogin={isLogin} handleDialogOpen={handleDialogOpen} />
			</Box>
			<DrawerComponents
				open={open}
				setOpen={setOpen}
				isLogin={isLogin}
				handleDialogOpen={handleDialogOpen}
				logout={logout}
			/>
			<ScrollUpButton />
			<RankingButton />
			<Dialog
				open={dialogOpen}
				onClose={handleDialogClose}
				style={{ zIndex: '1300' }}
			>
				<DialogTitle>pokemon-collector</DialogTitle>
				<DialogContent>   !</DialogContent>
				<DialogActions>
					<Button variant='contained' color='inherit' onClick={handleDialogClose}>
						
					</Button>
				</DialogActions>
			</Dialog>
		</Box>
	);
}

export default LandingPage;
