import React, { useState, useEffect } from 'react';
import {
  Card,
  CardMedia,
  IconButton,
  Typography,
  DialogActions,
  DialogContent,
  Button,
  Dialog,
  DialogTitle,
} from '@mui/material';
import { ArrowDropDown, HelpOutlineOutlined } from '@mui/icons-material';

import QuizText from './QuizItems/QuizText';
import QuizAnswerSubmit from './QuizItems/QuizAnswerSubmit';
import QuizSelect from './QuizItems/QuizSelect';
import QuizYesOrNo from './QuizItems/QuizYesOrNo';
import './QuizComponent.css';
import ImgSrc from '../../../core/constants/ImgSrc';

import * as Api from '../../../api';

function QuizComponent({
  pokemonId,
  pokemonName,
  chance,
  text,
  img = ImgSrc.quizImg1,
  isMobile,
  stage,
  nextStage,
  setStage,
}) {
  const [isHelpOpen, setIsHelpOpen] = useState(false);
  const [isAnswer, setIsAnswer] = useState(false);
  const [isHint, setIsHint] = useState(false);
  const [isPass, setIsPass] = useState(false);
  const [isStop, setIsStop] = useState(false);
  const [pokemonType, setPokemonType] = useState();
  const [pokemonNameNum, setPokemonNameNum] = useState();
  const [useAnswer, setUserAnswer] = useState();

  useEffect(() => {
    if (isHint) {
      Api.get(`pokemon/${pokemonId}`).then((res) => {
        setPokemonType(res.data.typeOne);
        setPokemonNameNum(pokemonName.length);
      });
    }
  }, [isHint]);

  const handleHelpClose = () => {
    setIsHelpOpen(false);
  };
  const handleHintClose = () => {
    setIsHint(false);
  };

  const checkAnswer = () => {
    //    -> 1000
    if (pokemonName === useAnswer && stage !== 'retry') {
      Api.get('succeedQuiz/first');
      setStage('firstCorrect');
      //    -> 500
    } else if (pokemonName === useAnswer) {
      Api.get('succeedQuiz/second');
      setStage('correct');
      //   -> 
    } else if (stage !== 'retry') {
      setStage('firstIncorrect');
      //   -> 
    } else {
      setStage('incorrect');
    }
  };

  const onClickYesPass = () => {
    setIsPass(false);
    setStage('quizStart');
  };
  const onClickNoPass = () => {
    setIsPass(false);
  };

  const onClickYesStop = () => {
    setIsStop(false);
    setStage('entry');
  };
  const onClickNoStop = () => {
    setIsStop(false);
  };

  const onClickYesContinue = () => {
    setStage('quizStart');
  };
  const onClickNoContinue = () => {
    setStage('entry');
  };

  return (
    <Card
      sx={{
        position: 'absolute',
        left: '50%',
        transform: 'translateX(-50%)',
        width: '60%',
        height: '70%',
        backgroundColor: '#fff',
        display: 'flex',
        flexDirection: 'column',
        justifyContent: 'spaceBetween',
        overflow: 'inherit',
      }}
    >
      <CardMedia
        component='img'
        image={img}
        alt=''
        sx={{
          width: '35%',
          maxWidth: '450px',
          minWidth: '200px',
          marginLeft: '50%',
          marginTop: '10px',
          transform: 'translateX(-50%)',
          filter: stage === 'quizIng' ? 'brightness(0%)' : 'none',
        }}
      />
      {stage !== 'quizIng' && stage !== 'retry' && <QuizText text={text} />}
      {(stage === 'quizIng' || stage === 'retry') &&
        !isAnswer &&
        !isPass &&
        !isStop && (
          <QuizSelect
            setIsAnswer={setIsAnswer}
            setIsHint={setIsHint}
            setIsPass={setIsPass}
            setIsStop={setIsStop}
          />
        )}
      {isAnswer && (
        <QuizAnswerSubmit
          setUserAnswer={setUserAnswer}
          checkAnswer={checkAnswer}
          setIsAnswer={setIsAnswer}
        />
      )}
      {isPass && <QuizText text='?' />}
      {isPass && (
        <QuizYesOrNo onClickYes={onClickYesPass} onClickNo={onClickNoPass} />
      )}
      {isStop && <QuizText text=' ?' />}
      {isStop && (
        <QuizYesOrNo onClickYes={onClickYesStop} onClickNo={onClickNoStop} />
      )}
      {stage === 'continue' && (
        <QuizYesOrNo
          onClickYes={onClickYesContinue}
          onClickNo={onClickNoContinue}
        />
      )}
      {stage !== 'quizIng' &&
        stage !== 'retry' &&
        stage !== 'continue' &&
        !isMobile && (
          <IconButton
            onClick={() => {
              setStage(nextStage);
            }}
            sx={{
              position: 'absolute',
              bottom: '20px',
              right: '20px',
            }}
          >
            <ArrowDropDown style={{ fontSize: '50px', color: 'black' }} />
          </IconButton>
        )}
      {(stage === 'quizStart' ||
        stage === 'quizIng' ||
        stage === 'retry' ||
        stage === 'continue') && (
        <img
          src={ImgSrc.pokeballIco}
          alt=''
          style={{
            position: 'absolute',
            top: '20px',
            left: '20px',
            width: '30px',
          }}
        />
      )}
      {(stage === 'quizStart' ||
        stage === 'quizIng' ||
        stage === 'retry' ||
        stage === 'continue') && (
        <div
          style={{
            position: 'absolute',
            top: '16px',
            left: '60px',
            fontSize: '24px',
          }}
        >
          X {chance}
        </div>
      )}
      {(stage === 'quizStart' ||
        stage === 'quizIng' ||
        stage === 'retry' ||
        stage === 'continue') && (
        <IconButton
          onClick={() => {
            setIsHelpOpen(true);
          }}
          sx={{
            position: 'absolute',
            top: '5px',
            right: '15px',
          }}
        >
          <HelpOutlineOutlined style={{ fontSize: '40px', color: 'black' }} />
        </IconButton>
      )}
      <Dialog
        open={isHelpOpen}
        onClose={handleHelpClose}
        style={{ zIndex: '10000' }}
      >
        <DialogTitle>  </DialogTitle>
        <DialogContent>
                  
           .
          <br />
            3  ,   1000
          .
          <br />
             ,    
            .
          <br />
              500 .
          <br />
                
          <br />
           ,  ,  ,   .
          <br />
                  ,
          <br />
                ,
          <br />
            1     .
          <br />
                  
          .
        </DialogContent>
        <DialogActions>
          <Button variant='contained' color='inherit' onClick={handleHelpClose}>
            
          </Button>
        </DialogActions>
      </Dialog>
      <Dialog
        open={isHint}
        onClose={handleHintClose}
        style={{ zIndex: '10000' }}
      >
        <DialogTitle></DialogTitle>
        <DialogContent style={{ textAlign: 'center' }}>
          <img
            alt=''
            src={ImgSrc.quizImg1}
            style={{ maxWidth: '400px' }}
          />
          <Typography variant='h5'>
              {pokemonNameNum} {pokemonType} !
          </Typography>
        </DialogContent>
        <DialogActions>
          <Button variant='contained' color='inherit' onClick={handleHintClose}>
            
          </Button>
        </DialogActions>
      </Dialog>
    </Card>
  );
}

export default QuizComponent;
