import React, { useState } from 'react';
import {
	Box,
	Button,
	TextField,
	Grid,
	Radio,
	RadioGroup,
	FormControlLabel,
	FormControl,
	InputLabel,
	Select,
	MenuItem,
	Typography,
	Rating,
	FormLabel,
} from '@mui/material';
import { CatchingPokemon } from '@mui/icons-material';
import LocalizationProvider from '@mui/lab/LocalizationProvider';
import AdapterDateFns from '@mui/lab/AdapterDateFns';
import DesktopDatePicker from '@mui/lab/DesktopDatePicker';
import IconObj from '../../../../core/Icon';

import * as Api from '../../../../api';

function RegisterPage({ setLogin }) {
	const [birth, setBirth] = useState();

	const [inputs, setInputs] = useState({
		email: '',
		password: '',
		nickname: '',
		sex: 'male',
		interest: 3,
		likeType: '',
		birth: '',
	});
	const types = [
		'',
		'',
		'',
		'',
		'',
		'',
		'',
		'',
		'',
		'',
		'',
		'',
		'',
		'',
		'',
		'',
		'',
	];

	const onChange = (e) => {
		const { name, value } = e.target;
		setInputs({
			...inputs,
			[name]: value,
		});
	};

	const validateEmail = (email) =>
		email
			.toLowerCase()
			.match(
				/^(([^<>()[\]\\.,;:\s@"]+(\.[^<>()[\]\\.,;:\s@"]+)*)|(".+"))@((\[[0-9]{1,3}\.[0-9]{1,3}\.[0-9]{1,3}\.[0-9]{1,3}\])|(([a-zA-Z\-0-9]+\.)+[a-zA-Z]{2,}))$/
			);
	const isEmailValid = validateEmail(inputs.email);
	//  4   .
	const isPasswordValid = inputs.password.length >= 4;
	//      .
	const isFormValid = isEmailValid && isPasswordValid;

	const handleSubmit = async (e) => {
		e.preventDefault();

		setInputs({
			...inputs,
			birth: Date(birth),
		});

		try {
			// "user/register"  post.
			await Api.post('user/register', inputs);

			//   .
			setLogin(true);
		} catch (err) {
			console.log(' .', err);
		}
	};

	return (
		<Box
			component='form'
			sx={{ display: 'flex', flexDirection: 'column' }}
			onSubmit={handleSubmit}
		>
			<TextField
				required
				autoFocus
				size='small'
				id='email'
				label=''
				name='email'
				autoComplete='email'
				onChange={onChange}
				sx={{
					width: '70%',
					margin: 'auto',
					marginBottom: '2%',
				}}
			/>
			<Typography
				variant='caption'
				className='text-success'
				sx={{ width: '70%', margin: 'auto', marginBottom: '1%' }}
				visibility={isEmailValid && 'hidden'}
			>
				   .
			</Typography>
			<TextField
				required
				name='password'
				size='small'
				label=''
				type='password'
				id='password'
				autoComplete='current-password'
				onChange={onChange}
				sx={{ width: '70%', margin: 'auto', marginBottom: '2%' }}
			/>
			<Typography
				className='text-success'
				sx={{ width: '70%', margin: 'auto', marginBottom: '1%' }}
				visibility={isPasswordValid && 'hidden'}
			>
				 4 .
			</Typography>
			<TextField
				required
				size='small'
				id='nickname'
				label=''
				name='nickname'
				autoComplete='nickname'
				onChange={onChange}
				sx={{ width: '70%', margin: 'auto', marginBottom: '1%' }}
			/>
			<Grid container spacing={2} sx={{ width: '90%', margin: 'auto' }}>
				<Grid item xs={5} md={5}>
					<FormControl sx={{ margin: 'auto' }} required>
						<FormLabel></FormLabel>
						<RadioGroup
							row
							defaultValue='male'
							name='sex'
							onChange={onChange}
							sx={{ minWidth: '300px' }}
						>
							<FormControlLabel value='male' control={<Radio />} label='' />
							<FormControlLabel value='female' control={<Radio />} label='' />
						</RadioGroup>
					</FormControl>
				</Grid>
				<Grid item xs={6} md={6}>
					<LocalizationProvider dateAdapter={AdapterDateFns}>
						<DesktopDatePicker
							label=''
							value={birth}
							disableFuture
							inputFormat='yyyy-MM-dd'
							mask='____-__-__'
							onChange={(newValue) => {
								setBirth(newValue);
								setInputs({
									...inputs,
									birth: Date(birth),
								});
							}}
							// eslint-disable-next-line react/jsx-props-no-spreading
							renderInput={(params) => <TextField {...params} />}
						/>
					</LocalizationProvider>
				</Grid>

				<Grid item xs={6} sm={6}>
					<Typography component='legend'>
						<CatchingPokemon />
						<span>  </span>
					</Typography>
					<Rating
						type='number'
						name='interest'
						value={Number(inputs.interest)}
						onChange={onChange}
					/>
				</Grid>
				<Grid item xs={6} sm={5}>
					<FormControl required fullWidth variant='standard' size='small'>
						<InputLabel id='demo-simple-select-label'> </InputLabel>
						<Select
							labelId='demo-simple-select-label'
							id='demo-simple-select'
							name='likeType'
							value={inputs.likeType}
							label='favAtt'
							onChange={onChange}
						>
							{types.map((type) => (
								<MenuItem key={type} value={type}>
									<Typography sx={IconObj[type].Color}>
										{IconObj[type].Icon} {type}
									</Typography>
								</MenuItem>
							))}
						</Select>
					</FormControl>
				</Grid>
			</Grid>

			<Button
				variant='contained'
				type='submit'
				color='success'
				sx={{ width: '40%', margin: 'auto', mt: 1, mb: 2 }}
				disabled={!isFormValid}
			>
				 
			</Button>
			<Grid container justifyContent='flex-end'>
				<Grid>
					<Button
						color='primary'
						onClick={() => {
							setLogin(true);
						}}
					>
						  ?  
					</Button>
				</Grid>
			</Grid>
		</Box>
	);
}

export default RegisterPage;
