import React, { useEffect, useState } from 'react';
import { useMediaQuery } from '@material-ui/core';

import QuizEntry from './components/QuizEntry';
import QuizComponent from './components/QuizComponent';
import ImgSrc, { pokemonURL } from '../../core/constants/ImgSrc';
import RankingButton from '../../components/commons/RankingButton';

import * as Api from '../../api';

function QuizPage() {
  const [stage, setStage] = useState('entry');
  // stage: entry, quizEx1~6, quizStart, quizIng, correct, firstCorrect, incorrect, firstIncorrect, retry, continue, noChance
  const [chance, setChance] = useState(3);
  const [pokemonName, setPokemonName] = useState();
  const [pokemonId, setPokemonId] = useState();
  const [pokemonImg, setPokemonImg] = useState();

  const isMobile = useMediaQuery('(max-width: 900px)');

  const fetchUserInfo = async () => {
    try {
      const res = await Api.get('quiz');
      setChance(res.data.reminingChance);
      setPokemonName(res.data.quiz.name);
      setPokemonId(res.data.quiz.id);
      setPokemonImg(`${pokemonURL}/${res.data.quiz.id}.png`);
    } catch (err) {
      setStage('noChance');
    }
  };

  useEffect(() => {
    if (stage === 'quizStart') {
      fetchUserInfo();
    }
  }, [stage]);

  return (
    <div style={{ paddingTop: '170px' }}>
      {!isMobile && stage === 'entry' && <QuizEntry setStage={setStage} />}
      {!isMobile && stage === 'quizEx1' && (
        <QuizComponent
          stage={stage}
          setStage={setStage}
          nextStage='quizEx2'
          text={[
            '    !',
            <br />,
            ' 151  .',
          ]}
        />
      )}
      {!isMobile && stage === 'quizEx2' && (
        <QuizComponent
          stage={stage}
          setStage={setStage}
          nextStage='quizEx3'
          text={[
            '  ',
            <br />,
            '     .',
          ]}
        />
      )}
      {!isMobile && stage === 'quizEx3' && (
        <QuizComponent
          stage={stage}
          setStage={setStage}
          nextStage='quizEx4'
          text='  1000   .'
        />
      )}
      {!isMobile && stage === 'quizEx4' && (
        <QuizComponent
          stage={stage}
          setStage={setStage}
          nextStage='quizEx5'
          text={[
            '     .',
            <br />,
            '  500 !',
          ]}
        />
      )}
      {!isMobile && stage === 'quizEx5' && (
        <QuizComponent
          stage={stage}
          setStage={setStage}
          nextStage='quizEx6'
          text={[' 3!', <br />, '    .']}
        />
      )}
      {!isMobile && stage === 'quizEx6' && (
        <QuizComponent
          stage={stage}
          setStage={setStage}
          nextStage='quizStart'
          text=',  !'
        />
      )}
      {!isMobile && stage === 'quizStart' && (
        <QuizComponent
          chance={chance}
          img={ImgSrc.pokeballImg}
          stage={stage}
          setStage={setStage}
          nextStage='quizIng'
          text='  !'
        />
      )}
      {!isMobile && (stage === 'quizIng' || stage === 'retry') && (
        <QuizComponent
          pokemonId={pokemonId}
          pokemonName={pokemonName}
          img={pokemonImg}
          chance={chance}
          stage={stage}
          setStage={setStage}
        />
      )}
      {!isMobile && stage === 'noChance' && (
        <QuizComponent
          stage={stage}
          setStage={setStage}
          nextStage='entry'
          text={[
            '   !',
            <br />,
            '  3  .',
          ]}
        />
      )}
      {!isMobile && stage === 'firstCorrect' && (
        <QuizComponent
          img={pokemonImg}
          stage={stage}
          setStage={setStage}
          nextStage='continue'
          text={[
            '! 1000 !',
            <br />,
            `  ${pokemonName}!`,
          ]}
        />
      )}
      {!isMobile && stage === 'correct' && (
        <QuizComponent
          img={pokemonImg}
          stage={stage}
          setStage={setStage}
          nextStage='continue'
          text={[
            '! 500 !',
            <br />,
            `  ${pokemonName}!`,
          ]}
        />
      )}
      {!isMobile && stage === 'firstIncorrect' && (
        <QuizComponent
          stage={stage}
          setStage={setStage}
          nextStage='retry'
          text={[
            '!  .',
            <br />,
            `   !`,
          ]}
        />
      )}
      {!isMobile && stage === 'incorrect' && (
        <QuizComponent
          img={pokemonImg}
          stage={stage}
          setStage={setStage}
          nextStage='continue'
          text={['...!', <br />, ` ${pokemonName}!`]}
        />
      )}
      {!isMobile && stage === 'continue' && (
        <QuizComponent
          isQuiz
          chance={chance}
          stage={stage}
          setStage={setStage}
          text='  ?'
        />
      )}
      {isMobile && <QuizComponent isMobile text=' pc!' />}
      <RankingButton />
    </div>
  );
}

export default QuizPage;
